#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 5px 20px;
}

.search {
  position: relative;
}

.search .search-input {
  height: 3.0625rem;
  width: 3.0625rem;
  color: #fff;
  letter-spacing: .03rem;
  cursor: pointer;
  background: none;
  border: .15rem solid #fff;
  border-radius: 3.0625rem;
  transition: all .3s;
}

.search .search-input:focus {
  outline: 0;
}

.search .cross {
  cursor: pointer;
  width: 27px;
  height: 27px;
  transition: all .3s .3s;
  position: absolute;
  top: 10px;
  right: 1rem;
  transform: translateX(31px)translateY(27px);
}

.search .cross:before, .search .cross:after {
  content: "";
  width: .15rem;
  height: 1.7rem;
  background: #fff;
  transition: all .3s;
  display: block;
  position: absolute;
  left: .8125rem;
}

.search .cross:before, .search .cross:after {
  transform: rotate(-45deg);
}

.search.opened .search-input {
  width: 20rem;
  cursor: text;
  transition-delay: .6s;
}

.search.opened .cross {
  transition-delay: 0s;
  transform: translateX(5px)translateY(1px);
}

.search.opened .cross:before {
  transition-delay: .3s;
  transform: rotate(45deg);
}

